<template>
  <!-- <v2-table :data="data">
    <v2-table-column label="Transaction ID" prop="id"></v2-table-column>
    <v2-table-column label="Sender" prop="sender"></v2-table-column>
    <v2-table-column label="Receiver" prop="receiver"></v2-table-column>  
    <v2-table-column label="Amount" prop="amount"></v2-table-column>  
    <v2-table-column label="Time" prop="created_time"></v2-table-column>  
  </v2-table>   -->
  <table class="pure-table pure-table-bordered">
    <thead>
        <tr>
            <th>Transaction ID</th>
            <th>Sender</th>
            <th>Receiver</th>
            <th>Amount</th>
            <th>Time</th>
        </tr>
        </thead>
        <tbody v-html=tbody_data>
         
          </tbody>  
</table>

</template>

<script>
import './../assets/pure-min.css';

export default {
  data() {
    return {
      tbody_data: ""
    };
  },
  mounted() {
    this.axios
      .get("http://localhost/api/v1/transactions")
      .then(response => {
        for(var i=0;i<response.data.length;i++){
          this.tbody_data += "<tr><td>"+ response.data[i].id+
          "</td><td>"+ response.data[i].sender+
          "</td><td>"+ response.data[i].receiver+
          "</td><td>"+ response.data[i].amount+
          "</td><td>"+response.data[i].created_time+
          "</td></tr>";
        }
      })
      .catch(response => {
        console.log(response);
      });
  }
};
</script>
